<template>
  <div id="mobileUserManagement">
    <Tree :data="treeData" :render="renderContent" style="padding: 5% 0">
    </Tree>
  </div>
</template>

<script>
import TreeRender from './TreeRender'
import {
  // 图层类型列表
  getLayerList,
  // 新增
  saveLayer,
  // 修改
  updateLayer,
  // 删除
  deleteLayer
} from '@/api/acquisitionTable'
export default {
  components: {},
  data () {
    return {
      treeData: [],
      // 树结构
      treeList: null
    }
  },
  props: {},
  computed: {},
  methods: {
    renderContent (h, { root, node, data }) {
      let that = this
      return h(TreeRender, {
        props: {
          treeRoot: root,
          treeNode: node,
          treeData: data
        },
        on: {
          nodeAdd: (d) => that.handleAdd(d),
          nodeEdit: (r, n, d) => that.handleEdit(r, n, d),
          nodeDel: (r, n, d) => that.handleDelete(r, n, d),
          nodeAfterEdit: (r, n, d) => that.handleAfterEdit(r, n, d)
        }
      })
    },
    /* axios: 获取图层类型菜单 */
    initTreeList () {
      getLayerList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.treeList = res.data.data
          this.getTree(this.treeList)
          this.treeData = this.treeList
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    // 递归解析
    getTree (parents) {
      if (parents === undefined) {
        return
      }
      parents.forEach(item => {
        item.title = item.name
        // item.expand = true
        this.getTree(item.children)
      })
    },
    // 编辑后，input框失去焦点
    handleAfterEdit (r, n, d) {
      // console.log('after')
      // console.log(d)
      d.name = d.title
      // console.log(d.name)
      let column = {}
      column.name = d.name
      column.id = d.id
      column.fid = d.fid
      column.level = d.level
      column.children = []
      // console.log(column)
      this.UpdateColumn(column)
    },
    // 编辑中，input框得到焦点
    handleEdit (r, n, d) {
      d.name = d.title
    },
    // 新增方法
    handleAdd (d) {
      console.log(d)
      //  构建一个实体
      let column = {}
      const children = d.children || []
      // 添加数据
      children.push({
        title: d.addData,
        expand: true,
        parent: d.id,
        // isEdit: false,
        children: []
      })
      column.name = d.addData
      column.fid = d.id
      column.level = d.level + 1
      column.children = []
      this.saveColumn(column)
    },
    saveColumn (column) {
      saveLayer({
        data: column
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '添加成功' })
          // 完成增操作后进行一次查询
          this.initTreeList()
        } else {
          this.$Notice.error({ title: res.data.msg })
          // 完成增操作后进行一次查询
          this.initTreeList()
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
        // 完成增操作后进行一次查询
        this.initTreeList()
      })
    },
    UpdateColumn (column) {
      updateLayer({
        data: column
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '修改成功' })
          // 改操作后进行一次查询
          this.initTreeList()
        } else {
          this.$Notice.error({ title: res.data.msg })
          // 完成增操作后进行一次查询
          this.initTreeList()
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
        // 完成增操作后进行一次查询
        this.initTreeList()
      })
    },
    handleDelete (r, n, d) {
      const parentKey = r.find(el => el === n).parent
      const parent = r.find(el => el.nodeKey === parentKey).node
      const index = parent.children.indexOf(d)
      parent.children.splice(index, 1)
      let id = d.id
      this.deleteColumn(id)
    },
    // 根据id查询
    deleteColumn (id) {
      deleteLayer({
        data: {
          id: [id]
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '删除成功' })
          // 改操作后进行一次查询
          this.initTreeList()
        } else {
          this.$Notice.error({ title: res.data.msg })
          // 改操作后进行一次查询
          this.initTreeList()
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
        // 改操作后进行一次查询
        this.initTreeList()
      })
    }
  },
  watch: {},
  created () {
    this.initTreeList()
  },
  mounted () {}
}
</script>

<style scoped lang="scss">
  #mobileUserManagement {
    position: relative;
    .ivu-tree {
      padding: 0 5%;
      width: 500px;
      margin: 3% auto 0;
      text-align: left;
      overflow: auto;
      max-height: 400px;
      .ivu-tree-arrow {
        float: left !important;
        margin-right: 10px;
      }
    }
  }
</style>
